/* latin */
@font-face {
  font-family: "Cabaret";
  font-style: normal;
  font-weight: 300;
  src: url(/Cabaret.ttf) format("truetype"), url("/Cabaret.woff") format("woff");
}
.nfts-page {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  font-family: Cabaret;
  font-size: 12pt;
  margin: auto;
  margin-top: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}
.nfts-page .projects-header {
  font-size: 16pt;
}
.nfts-page .projects-list {
  margin-top: 25px;
  -ms-flex: 1 1;
      flex: 1 1;
}
.nfts-page .projects-list .minted-project:hover {
  color: red;
}
.nfts-page .projects-list .minted-project {
  cursor: pointer;
  width: 800px;
  display: -ms-flexbox;
  display: flex;
}
.nfts-page .projects-list .minted-project .author {
  margin-left: auto;
  margin-right: 25px;
}
.nfts-page .projects-list .mint:hover {
  color: red;
}
.nfts-page .projects-list .mint {
  display: -ms-flexbox;
  display: flex;
  margin-left: 25px;
  cursor: pointer;
}
.nfts-page .projects-list .mint .token-icon {
  margin-right: 25px;
}
.nfts-page .projects-list .mint-button {
  background-color: white;
  color: black;
  cursor: pointer;
  padding: 10px;
  width: 200px;
  text-align: center;
  font-size: 12pt;
}
.nfts-page .projects-list .progress-bar {
  position: unset;
  left: unset;
  -webkit-transform: unset;
          transform: unset;
}
