/* latin */
@font-face {
  font-family: 'Cabaret';
  font-style: normal;
  font-weight: 300;
  src: url(/Cabaret.ttf) format('truetype'),
  url('/Cabaret.woff') format('woff');
}


.nfts-page {
    flex: 1;
    height: 100%;
    font-family: Cabaret;
    font-size: 12pt;
    margin: auto;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    height: 100%;

    .projects-header {
        font-size: 16pt;
    }

    .projects-list {
        margin-top: 25px;
        flex: 1;


        .minted-project:hover {
            color: red;
        }

        .minted-project {
            cursor: pointer;
            width: 800px;
            display: flex;

            .author {
                margin-left: auto;
                margin-right: 25px;
            }

            .mints {
            }

        }

        .mint:hover {
            color: red;
        }
        
        .mint {
            display: flex;
            margin-left: 25px;
            cursor: pointer;
            .token-icon {
                margin-right: 25px;
            }
        }

        .mint-button {
            background-color: white;
            color: black;
            cursor: pointer;
            padding: 10px;
            width: 200px;
            text-align: center;
            font-size: 12pt;
        }

        .progress-bar {
            position: unset;
            left: unset;
            transform: unset;
        }
    }
}
